import * as React from 'react';
import { useEffect, useRef, useState } from 'react';

import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet.markercluster';
import 'leaflet.markercluster/dist/MarkerCluster.css';
import icon from 'leaflet/dist/images/marker-icon.png';
import iconShadow from 'leaflet/dist/images/marker-shadow.png';

import {Grid, Paper, Divider, Typography, Slider, Box, OutlinedInput, InputLabel, MenuItem, FormControl, FormGroup, FormControlLabel, ListItemText, Select, Checkbox, Switch, Button} from '@mui/material';

import data from '../assets/data.json';

function MapFComponent(props){

    /* Proprits d'affichage */
    const ITEM_HEIGHT = 48;
    const ITEM_PADDING_TOP = 8;
    const MenuProps = {PaperProps: {style: {maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP, width: 250,},},};
    const secteurs = ['Entrepot', 'Bureau', 'Parking', 'Logement'];

    /* Initialisation du state*/
    // Filtres
    const [annee, setAnnee] = useState(2016);
    const [surface, setSurface] = useState(5000);
    const [nouveauxSites, setNouveauxSites] = useState(true);
    const [dejaVisites, setDejaVisites] = useState(false);
    const [listeSecteurs, setListeSecteurs] = useState([]);
    // Site consult
    const [detailsOuverts, setDetailsOuverts] = useState(false);
    const [cleSelectionnee, setCleSelectionnee] = useState("01306321G0031");
    const [zoneUrba, setZoneUrba] = useState('chargement...');
    const [empriseSite, setEmpriseSite] = useState([]);

    // Bindings leaflet
    const [mapInstance, setMapInstance] = useState(null);

    const mapRef = useRef(null);
    const empriseRef = useRef(null);
    const firstUpdate = useRef(true);

    /* Appel API zone-urba */
    const getZoneUrba = () => {
        if (firstUpdate.current) {firstUpdate.current = false; return;} // Pour empcher un appel API au premier rendu
        console.log(`Dbut GetZoneUrba ${cleSelectionnee}`)
        let payload = `{"type":"Point","coordinates":[${data[cleSelectionnee]['lon']},${data[cleSelectionnee]['lat']}]}`;
        var urlZoneUrba = new URL("https://apicarto.ign.fr/api/gpu/zone-urba");
        urlZoneUrba.searchParams.append('geom', payload);
        /*    $.get(urlZoneUrba,
            {geom:payload},
            ( data ) => {console.log(data); alert( "Load was performed." );});*/

        //console.log(urlZoneUrba);

        fetch(urlZoneUrba).then(
            (result) => {
                if (result.status != 200) { throw new Error("Bad Server Response"); }
                return result.json();
            }).then((response) => {
                console.log('Result.json()');
                console.log(response);
                //console.log(`TotalFeatures ${response.totalFeatures}`);

                switch (response.totalFeatures) {
                case 0:
                    setZoneUrba('Aucune zone trouve !');
                    break;
                    case 1:
                    console.log(`${response.features[0].properties.libelong}`);
                    setZoneUrba(`${response.features[0].properties.libelong}`);
                    setEmpriseSite(response.features[0].geometry.coordinates[0][0]);
                    break;
                default:
                    setZoneUrba('Trop de rsultats !');
                    break;
            }
        })

    };


    /* Clic sur marqueur Leaflet */
    const onMarkerClick = (e) => {
        console.log('Marker click !')
        let key = e.sourceTarget.options.title;

        setZoneUrba('Chargement...');
        setDetailsOuverts(true);

        if (mapRef.current)  {
            console.log(mapRef.current.getBounds());
            mapRef.current.flyTo(e.target.getLatLng(),14);
        }

        setCleSelectionnee(key);
    };

    /* Gestion de l'appel async de zone-urba*/
    useEffect(() => {
        getZoneUrba();
        }, [cleSelectionnee]);

    /* Affichage de l'emprise de la zone d'urbanisme */
    useEffect(() => {
        if (!mapInstance) return;
        // Nettoyage d'un eventuel polygone dj affich
        if (empriseRef.current) {
            empriseRef.current.removeFrom(mapInstance);
        }
        // Affichage d'un nouveau polygone
        empriseRef.current = L.polygon(empriseSite.map(e => [e[1],e[0]])).addTo(mapInstance); // latitude et longitude sont inverses pour leaflet par rapport aux retours des API GeoPortail.

    }, [empriseSite]);


    /* [BOUCHON] Application des filtres -  remplacer par un appel  une BDD SQL pour rcuprer un nouveau json data contenant toutes les informations SITADEL */
    const applyFilters = () => {
        const msg = `Filtres :\nAnne ${annee}\nSurface ${surface}\nNouveaux sites ${nouveauxSites}\nDj visits ${dejaVisites}\nType ${listeSecteurs}`;
        alert(msg);
    };

    /* Slecteur de secteur */
    const handleChange = (event) => {
            const {
              target: { value },
            } = event;
            setListeSecteurs(typeof value === 'string' ? value.split(',') : value)
    };

    /* Initialisation de la carte // Runs only on the first render */
    useEffect(() => {

        let myIcon = L.icon({
            iconUrl: icon,
            shadowUrl: iconShadow,
            iconSize: [25, 41],
            iconAnchor: [13, 41],
            popupAnchor: [0, 0],
            shadowSize: [41, 41],
            shadowAnchor: [13, 41]
        });

        //const position0 = [43.5626632, 5.0249413];
        //const key = "01306321G0031";

        mapRef.current = L.map('map', {
            center: [48.8481232,2.351374], // position EDIT coordinates to re-center map
            zoom: 12, // 6 // EDIT from 0 (zoomed out) to 18 (zoomed in)
            scrollWheelZoom: true,
            tap: false
        });
        /*var tiles = L.tileLayer('https://api.mapbox.com/styles/v1/{id}/tiles/{z}/{x}/{y}?access_token=pk.eyJ1IjoibWFwYm94IiwiYSI6ImNpejY4NXVycTA2emYycXBndHRqcmZ3N3gifQ.rJcFIG214AriISLbB6B5aw', {
            maxZoom: 18,
            attribution: 'Imagery &copy; <a href="https://www.mapbox.com/">Mapbox</a>',
            id: 'mapbox/streets-v11',
            tileSize: 512,
            zoomOffset: -1
        }).addTo(map);*/ // MapBox Tiles
        L.tileLayer('https://{s}.tile.openstreetmap.fr/osmfr/{z}/{x}/{y}.png', {
            maxZoom: 18,
            attribution: '&copy; <a href="https://openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        }).addTo(mapRef.current);

        //L.marker(position, {icon: myIcon, title: key}).on('click', onMarkerClick).addTo(map);

        var markersCluster = new L.MarkerClusterGroup();

        for (let key in data) {
            var position = new L.LatLng(data[key]['lat'], data[key]['lon']);
            var marker = new L.Marker(position, {icon: myIcon, title: key});
            marker.on('click', onMarkerClick);
            markersCluster.addLayer(marker);
        }

        mapRef.current.addLayer(markersCluster);

        setMapInstance(mapRef.current);
    }, []);

    /* Rendu du composant React */
    return(
        <Grid container spacing={2} sx={{height : 'calc(100% + 16px)'}}>
                <Grid item xs={detailsOuverts ? 4 : 8} sx={{height : '100%'}}>
                    <Paper sx={{height : '100%'}} id='map'>
                    </Paper>
                </Grid>
                {/* Filtres */}
                <Grid item xs={detailsOuverts ? 8 : 4} sx={{ height : '100%'}}>
                    {detailsOuverts ?
                        <Paper sx={{height: '100%', width: 'calc(100%-16px)', overflow : 'auto', pt: 0, pl: '8px', pr: '8px'}}>
                            <Box sx={{position: 'sticky', zIndex: 100, top: '0', backdropFilter: 'blur(10px)', boxShadow: "inset 0px -1px 1px #e7ebf0", backgroundColor: 'rgba(211,47,47,0.7)', margin: '0 -8px 0px -8px', textAlign: 'center'}}>
                                <Button variant='text' sx={{width: '100%', color: '#ffffff'}} onClick={() => {
                                    setDetailsOuverts(false)
                                }}>
                                    Revenir aux filtres
                                </Button>
                            </Box>
                            Identifiant de la DAU : {cleSelectionnee}
                            <br/>
                            Rcupr sur GeoPortail : {zoneUrba}
                            <br/>
                            <br/>
                            <Divider/>
                            <br/>
                            Informations SITADEL<br/>
                            {Object.keys(data[cleSelectionnee]).map((keyName, i) => (
                                <div key={keyName}>
                                    {keyName} : {data[cleSelectionnee][keyName]}
                                    <br/>
                                </div>
                            ))}
                            <br/>
                            <br/>
                            <Divider/>
                            <br/>
                            Lorem ipsum dolor sit amet, consectetuer adipiscing elit, sed diam nonummy nibh euismod tincidunt ut laoreet dolore magna aliquam erat volutpat. Ut wisi enim ad minim veniam, quis nostrud exerci tation ullamcorper suscipit lobortis nisl ut aliquip ex ea commodo consequat. Duis autem vel eum iriure dolor in hendrerit in vulputate velit esse molestie consequat, vel illum dolore eu feugiat nulla facilisis at vero eros et accumsan et iusto odio dignissim qui blandit praesent luptatum zzril delenit augue duis dolore te feugait nulla facilisi. Nam liber tempor cum soluta nobis eleifend option congue nihil imperdiet doming id quod mazim placerat facer possim assum. Typi non habent claritatem insitam; est usus legentis in iis qui facit eorum claritatem.

                        </Paper>
                        : <Paper sx={{height: '100%', overflow: 'auto', pt: 0, pl: '8px', pr: '8px'}}>
                        <Box sx={{
                            position: 'sticky',
                            zIndex: 100,
                            top: '0',
                            backdropFilter: 'blur(10px)',
                            boxShadow: "inset 0px -1px 1px #e7ebf0",
                            backgroundColor: 'rgba(21,101,192,0.7)',
                            margin: '0 -8px 0px -8px',
                            textAlign: 'center'
                        }}>
                            <Button variant='text' sx={{width: '100%', color: '#ffffff'}} onClick={() => {
                                applyFilters();
                            }}>
                                Appliquer
                            </Button>
                        </Box>
                        <br/>
                        <Typography>Anne</Typography>
                        <Slider aria-label="Temperature" value={annee}
                                getAriaValueText={(value) => '$value'}
                                valueLabelDisplay="on"
                                marks
                                min={2013} max={2021} step={1}
                                onChange={(e, value) => {
                                    setAnnee(value)
                                }}/>

                        <Typography>Surface minimale</Typography>
                        <Slider aria-label="slider2" value={surface}
                                getAriaValueText={(value) => `${value}`}
                                valueLabelDisplay="on"
                                min={0} max={10000} color="secondary"
                                onChange={(e, value) => {
                                    setSurface(value)
                                }}
                        />

                        <FormGroup>
                            <FormControlLabel control={<Switch checked={nouveauxSites} color='warning'
                                                               onChange={(e, value) => {
                                                                   setNouveauxSites(value)
                                                               }}/>}
                                              label="Nouveaux sites uniquement"/>
                            <FormControlLabel control={<Switch checked={dejaVisites}
                                                               onChange={(e, value) => {
                                                                   setDejaVisites(value)
                                                               }}/>}
                                              label="Sites dj visits"/>
                        </FormGroup>

                        <FormControl sx={{m: 1, width: 'clamp(50px, 300px, calc(100% - 24px))'}}>
                            <InputLabel id="demo-multiple-checkbox-label">Secteur</InputLabel>
                            <Select
                                labelId="demo-multiple-checkbox-label"
                                id="demo-multiple-checkbox"
                                multiple
                                value={listeSecteurs}
                                onChange={handleChange}
                                input={<OutlinedInput label="Secteur"/>}
                                renderValue={(selected) => selected.join(', ')}
                                MenuProps={MenuProps}
                            >
                                {secteurs.map((sec) => (
                                    <MenuItem key={sec} value={sec}>
                                        <Checkbox checked={listeSecteurs.indexOf(sec) > -1}/>
                                        <ListItemText primary={sec}/>
                                    </MenuItem>
                                ))}
                            </Select>
                        </FormControl>
                    </Paper>}
                </Grid>

            </Grid>
    );
}

export default MapFComponent;