import {Map, CalendarViewWeek, Email, InsertDriveFile, Engineering, Preview, Home} from '@mui/icons-material';
import {Box, Link, Typography, List, Divider, ListItemButton, ListItemIcon, ListItemText, Container, ListItem} from '@mui/material';

import MapComponent from './MapComponent';
import MapFComponent from "./MapFComponent";

import {Link as RouterLink} from "react-router-dom";
import * as React from "react";

const xsToggleListText = {display : {xs: 'none', md: 'inherit'}, pl: '16px', pr: '16px', width: '50px'};

function App2() {
    return (
        <Box sx={{height: '100vh'}}>
            {/* Menu */}
            <Box component='header' sx={{position:'sticky', top:0, backdropFilter: 'blur(20px)', boxShadow: "inset 0px -1px 1px #e7ebf0", backgroundColor: '#4a78cf'}}>
                <Container maxWidth='lg' sx={{display : 'flex', flexDirection : 'row', alignItems : 'center', minHeight:'56px', color: '#ffffff'}}>
                    <Engineering  fontSize='large' sx={{mr:'20px'}}/>
                    <Box sx={{fontWeight: 700, padding:'10px'}}>Prospection</Box>
                    <Link component={RouterLink} to="/" sx={{ml: 'auto'}} color='inherit'>
                        <Home fontSize='large'/>
                    </Link>
                </Container>
            </Box>
            <Box component='main' sx={{display : 'flex', flexDirection: 'row', alignItems: 'top', height: 'calc(100% - 56px)'}}>
                <Box sx={{borderWidth: '0px', borderRightWidth: 'thin', borderStyle: 'solid', borderColor: 'rgba(0, 0, 0, 0.12)', overflow:'auto'}}>
                    <List>
                      <ListItemButton key="mapDrawerButton" selected={true}>
                          <Map />
                          <ListItemText primary="Carte" sx={xsToggleListText}/>
                      </ListItemButton>
                      <ListItemButton key="tableDrawerButton">
                          <CalendarViewWeek />
                          <ListItemText primary="Tableau" sx={xsToggleListText}/>
                        </ListItemButton>
                        <Divider />
                        <ListItemButton key="mailDrawerButton">
                              <Email />
                              <ListItemText primary="Mail" sx={xsToggleListText}/>
                        </ListItemButton>
                        <ListItemButton key="pdfDrawerButton">
                            <InsertDriveFile />
                            <ListItemText primary="PDF" sx={xsToggleListText}/>
                        </ListItemButton>
                    </List>
                </Box>
                <Box sx={{width: {xs: 'calc(100% - 56px)', md: 'calc(100% - 138px)'}, padding: '16px'}}>
                    <MapFComponent/>
                </Box>
            </Box>

        </Box>
    )
}

export default App2;